import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms'; 
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SalesDiscountComponent } from './sales-discount.component';
import { RouterModule, Routes } from '@angular/router'; 
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { ListDiscountComponent } from './list-discount/list-discount.component';
import { ShowReportComponent } from './show-report/show-report.component';

const routes: Routes = [
  {
    path: '',
    component: SalesDiscountComponent
  }, 
  {
    path: 'list-discount',
    component: ListDiscountComponent
  }, 
  {
    path: 'show-report',
    component: ShowReportComponent
  }, 
]



@NgModule({
  declarations: [
    SalesDiscountComponent, 
    ShowReportComponent,
    ListDiscountComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule,
    FormsModule,
    ReactiveFormsModule
  ]
})
export class SalesDiscountModule { }
