import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-sales-discount',
  templateUrl: './sales-discount.component.html',
  styleUrls: ['./sales-discount.component.scss']
})
export class SalesDiscountComponent implements OnInit {

  constructor(
    private router:Router
  ) { }

  ngOnInit(): void {
  }

  gotoListDiscount(){
    this.router.navigate(['/sales/dashboard/manage/sales-program/discount-employee/sales-discount/list-discount'])
  }

  gotoReportDiscount(){
    this.router.navigate(['/sales/dashboard/manage/sales-program/discount-employee/sales-discount/show-report'])

  }
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/sales-program/discount-employee'])
  }

}
